.wrapp {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #4b5fb7;
    z-index: 10;
}

.search-form {
    position: relative;
    align-items: center;
}

input[name='searchQuery'] {
    padding: 5px;
    height: 30px;
    border-radius: 7px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14);
}

button[type='submit'] {
    position: absolute;
    top: 5px;
    right: 0;
    background-color: transparent;
    border: none;
}

button[type='submit']:hover .search-svg {
    fill: #4b5fb7;
}

/* -----------------IMG--------------- */

.gallery {
    display: flex;
    flex-wrap: wrap;
    max-width: 1230px;
    margin-right: auto;
    margin-left: auto;
    padding-top: 60px;
    padding-left: 15px;
    padding-right: 15px;
}

.photo-link {
    color: black;
    text-decoration: none;
    width: calc(100% / 4 - 14px);
    margin: 7px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
        0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    transition: transform, 250ms, linear;
}

.photo-link:hover {
    transform: scale(1.03);
}

.img-thumb {
    width: 100%;
    height: 187px;
}

img {
    display: inline-block;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.info {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-left: -15px;
}

.info-item {
    text-align: center;
    font-size: 10px;
    width: calc(100% / 4 - 14px);
}

.load-more {
    display: block;
    margin: 10px auto;
    border: 2px solid #4b95b7;
    border-radius: 7px;
    background-color: transparent;
}

.load-more:hover {
    background-color: #4b5fb7;
    border: 2px solid inherit;
}

.load-more.is-hidden {
    display: none;
}